import React from 'react';
import { string, shape } from 'react-proptypes';

import Icon from '../../../components/Icon';
import { SignupForm } from '../../../components/SignupForm';

import './MainBlock.scss';

const MainBlock = ({ params }) => (
    <div className="main-block__container">

        <div className="icon__container">
            <Icon image="robin" />

            <div className="message__container copytext">
                {params.message}
            </div>
        </div>

        <h1 dangerouslySetInnerHTML={{ __html: params.title }} />

        <p className="main-block__description subline2"
        dangerouslySetInnerHTML={{ __html: params.description }} />

        <SignupForm />

        <p className="small-label main-block__terms" 
            dangerouslySetInnerHTML={{ __html: params.terms }} />
    </div>
);

MainBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       inputPlaceholder: string,
       buttonText: string
    })
};

export default MainBlock;



